import React, { Component } from 'react';
import {CopyToClipboard} from 'react-copy-to-clipboard';
export default class Header extends Component {
	state = { value: 'das.thimothe@gmail.com', copied: false };
 onChange = ({ target: { value } }) => {
    this.setState({ value, copied: false });
  };

  onClick = ({ target: { innerHTML } }) => {
    console.log(`Clicked on "${innerHTML}"!`); // eslint-disable-line
  };

  onCopy = () => {
    this.setState({ copied: true });
  };
  render() {
	  let resumeData = this.props.resumeData;
	  
    return (
      <React.Fragment>
      <header id="home">
  <nav id="nav-wrap">
    <a className="mobile-btn" href="#nav-wrap" title="Show navigation">Show navigation</a>
    <a className="mobile-btn" href="#" title="Hide navigation">Hide navigation</a>
    <ul id="nav" className="nav">
      <li className="current"><a className="smoothscroll" href="#home">Accueil</a></li>
      <li><a className="smoothscroll" href="#about">A propos</a></li>
	  <li><a className="smoothscroll" href="#quiz">Quizz</a></li>
      <li><a className="smoothscroll" href="#resume">CV</a></li>
      <li><a className="smoothscroll" href="#portfolio">Projets</a></li>
      <li><a className="smoothscroll" href="#testimonials">Témoignages</a></li>
	  { /* <li><a className="smoothscroll" href="#contact">Contact</a></li> */ }
    </ul> {/* end #nav */}
  </nav> {/* end #nav-wrap */}
  <div className="row banner">
    <div className="banner-text">
      <h1 className="responsive-headline">Je m'apelle {resumeData.name}.</h1>
      <h3> {resumeData.role} {resumeData.roleDescription}</h3>
      
      <ul className="social">
	  <CopyToClipboard
          onCopy={this.onCopy}
          options={{ message: 'Whoa!' }}
          text={this.state.value}>
        <li><a href="#home"><i className="fa fa-envelope" /></a></li>
		</CopyToClipboard>
        <li><a target="_blank" href="https://www.linkedin.com/in/thimothé-das-3aa44a11a"><i className="fa fa-linkedin" /></a></li>
        <li><a target="_blank" href="https://join.skype.com/invite/clDazY7bCyFt"><i className="fa fa-skype" /></a></li>
      </ul>
<section className="messagecopie">
          {this.state.copied ? <span style={{ color: 'white' }}>Mon adresse mail est enregistré dans votre presse papier.
		  Collez la où vous le voulez.</span> : null}
        </section>
    </div>
  </div>
  <p className="scrolldown">
    <a className="smoothscroll" href="#about"><i className="icon-down-circle" /></a>
  </p>
</header>
      </React.Fragment>
    );
  }
}