import React, { Component } from 'react';
export default class Testimonials extends Component {
      render() {
    return (
      <React.Fragment>
      <section id="testimonials">
  <div className="text-container">
    <div className="row">
      <div className="two columns header-col">
        <h1><span>Client Testimonials</span></h1>
      </div>
      <div className="ten columns flex-container">
        <div className="flexslider">
          <ul className="slides">
            <li>
              <blockquote>
                <p>Thimothé  a su s'adapter rapidement à l'environnement très spécifique de Gaming Jobs, 
				à savoir l'insertion professionnelle dans un secteur aussi jeune et faiblement structuré que celui du jeu vidéo et de l'esport.
                </p>
                <cite>Matthieu Leclère - CEO de gaming Jobs</cite>
              </blockquote>
            </li> {/* slide ends */}
            <li>
              <blockquote>
                <p>Thimothé montre une appétence particulière pour les enseignements liés à la stratégie
numérique tant dans la dimension opérationnelle de la gestion de projet que dans la dimension
analytique. Il a obtenu de très bons résultats dans l'analyse de communautés numériques [...] il a construit son parcours lié à la communication numérique lors de diverses expériences
professionnelles en s'investissant au sein d'une entreprise pour développer l'image de marque,
créer et animer une communauté.
                </p>
                <cite>Mme Mellot - Maître de conférence, UCO</cite>
              </blockquote>
            </li> {/* slide ends */}
          </ul>
        </div> {/* div.flexslider ends */}
      </div> {/* div.flex-container ends */}
    </div> {/* row ends */}
  </div>  {/* text-container ends */}
</section>
      </React.Fragment>
    );
  }
};