import React from 'react';
import PropTypes from 'prop-types';
import { CSSTransitionGroup } from 'react-transition-group';
import Pie from '../components/Pie';
import App from '../App';


function Result(props) {

  return (
    <CSSTransitionGroup
      className="container result"
      component="div"
      transitionName="fade"
      transitionEnterTimeout={800}
      transitionLeaveTimeout={500}
      transitionAppear
      transitionAppearTimeout={500}
    >
      <div>
        Mon profil est compatible à <strong>{props.quizResult}%</strong> avec vos attentes !
     </div>
	 <div>
        <div
          className="box"  
        >
          <Pie
            color="#01579B"
            width={props.quizResult}
            progress={props.quizResult / 100}
			// ca marche avec this.state.division2 ou division mais ca affiche que state est null
          />
        </div>
      </div>
    </CSSTransitionGroup>
  );

}

Result.propTypes = {
  quizResult: PropTypes.string.isRequired
};

export default Result;
