import React, { Component } from 'react';
import {CopyToClipboard} from 'react-copy-to-clipboard';
export default class Footer extends Component {
	state = { value: 'das.thimothe@gmail.com', copied: false };
 onChange = ({ target: { value } }) => {
    this.setState({ value, copied: false });
  };

  onClick = ({ target: { innerHTML } }) => {
    console.log(`Clicked on "${innerHTML}"!`); // eslint-disable-line
  };

  onCopy = () => {
    this.setState({ copied: true });
  };
  render() {
    return (
      <React.Fragment>
      <footer id="foooter">
  <div className="row">
    <div className="twelve columns">
      <ul className="social-links">
        <li><a target="_blank" href="https://www.linkedin.com/in/thimothé-das-3aa44a11a"><i className="fa fa-linkedin" /></a></li>
        <CopyToClipboard
          onCopy={this.onCopy}
          options={{ message: 'Whoa!' }}
          text={this.state.value}>
		<li><a href="#footer" ><i className="fa fa-envelope" /></a></li>
		</CopyToClipboard>
        <li><a target="_blank" href="https://join.skype.com/invite/clDazY7bCyFt"><i className="fa fa-skype" /></a></li>
      </ul>
	  <section className="messagecopie1">
          {this.state.copied ? <span style={{ color: 'white' }}>Mon adresse mail est enregistré dans votre presse papier.
		  Collez la où vous le voulez.</span> : null}
        </section>
		{ /* <ul className="copyright">
        <li>© Copyright 2018</li>
        <li>Design by Thimothé DAS <a title="" href=""></a></li>   
		</ul> */ }
    </div>
    <div id="go-top"><a className="smoothscroll" title="Back to Top" href="#home"><i className="icon-up-open" /></a></div>
  </div>
</footer>
      </React.Fragment>
    );
  }
}