import React, { Component } from 'react';
import Counter from '../components/Counter';

class Pie extends React.Component {
  render() {
    const { color, width, progress } = this.props;
    const pi = 3.14159265359;
    const r = (400 / 2);
    const c = (2 * pi) * r;
    const realProgress = c * progress;
    return (
      <div className="svg-container"> 
        <div
          className="svg-content"
        >
          <svg
            viewBox="0 0 500 500"
            preserveAspectRatio="xMinYMin meet"
          >
            <circle
              className="animated"
              cx="250"
              cy="250"
              r="200"
              stroke={"#3E2723"}
              fillOpacity="0"
              strokeWidth={width + 20}
              strokeDasharray={[realProgress,  c]}
              strokeDashoffset={c * progress}
            />
            <circle
              className="animated"
              cx="250"
              cy="250"
              r="200"
              stroke={color}
              fillOpacity="0"
              strokeWidth={width}
              strokeDasharray={[realProgress,  c]}
              strokeDashoffset={c * progress}
            />
            
            <Counter
              width={width}
              progress={progress * 100}
            />
            <circle
              cx="250"
              cy="250"
              r="200"
              stroke={color}
              fillOpacity="0"
              strokeWidth={width}
              strokeDasharray={[c,  c]}
              strokeOpacity="0.5"
            />
            
          </svg>
        </div>
      </div>
    );
  }
}
export default Pie ;