import React, { Component } from 'react';
export default class Resume extends Component {
  render() {
    return (
      <React.Fragment>
      <section id="resume">
  {/* Education
----------------------------------------------- */}
  <div className="row education">
    <div className="three columns header-col">
      <h1><span>Education</span></h1>
    </div>
    <div className="nine columns main-col">
      <div className="row item">
        <div className="twelve columns">
          <h3>Bordeaux Montaigne</h3>
          <p className="info">Master 1 Conception de projets numériques et multimédia<span>•</span> <em className="date">Septembre 2018</em></p>
          <p> Le master rassemble des cours de developpement web (PHP, HTML, CSS), de gestion de projets mais aussi de 
		  veille stratégique et de méthodologie de la recherche pour permettre une recherche de l'information plus efficace.
          </p>
		  <a target="_blank" href="https://www.u-bordeaux-montaigne.fr/fr/formations/offre-de-formation-2016-2020/master-XB/information-communication-et-mediation-des-sciences-SCINFO.9/master-conception-de-projet-numerique-et-multimedia-program-msm16-216.html">
		  <img className="logocv3" src="images/Universite_bordeaux-montaigne.png" alt />
			</a>
		</div>
      </div> {/* item end */}
      <div className="row item">
        <div className="twelve columns">
          <h3>Université Catholique de l'Ouest</h3>
          <p className="info">Licence 1, 2 et 3 en Information et Communication mineure Scicens Politiques <span>•</span> <em className="date">Septembre 2015, 2016, 2017</em></p>
          <p>
            Cette licence m'a permis d'acquérir des bases en communication d'entreprise, en réalisation de projets marketing et de communication ainsi qu'en réalisation de dossier analytique 
			sur des projets d'entreprise ou sur des echantillons de personnes spécifiques (communauté, nationalité etc...). La mineure en Sciences politiques m'a permis
			d'obtenir une culture politique et sociale plus précise qui m'est particulièrement utile.
			
          </p>
		  <a target="_blank" href="https://www.uco.fr/fr/formations/humanites/licence-information-communication">
		  <img className="logocv4" src="images/logo_UCO.png" alt />
        </a>
		</div>
      </div> {/* item end */}
    </div> {/* main-col end */}
  </div> {/* End Education */}
  {/* Work
----------------------------------------------- */}
  <div className="row work">
    <div className="three columns header-col">
      <h1><span>Experience professionnelle</span></h1>
    </div>
    <div className="nine columns main-col">
      <div className="row item">
        <div className="twelve columns">
          <h3>Gaming Jobs</h3>
          <p className="info">Chargé de communication et marketing<span>•</span> <em className="date">Avril 2018</em></p>
          <p> J'ai eu l'occasion de réaliser un tutoriel ainsi qu'une interview professionnelle destiné à être publié sur le site. Une veille concurentielle 
		  avec des préconisations comme une FAQ ont aussi été rédigées par mes soins. Aussi, j'ai été chargé  de réaliser une analyse des comportements 
		  des visiteurs du site, j'ai notamment réaliser l'analyse des données et leur croisements sous Excel. Je n'ai cependant pas participé à sa mise en forme
		  via Photoshop, Indesign etc...
		  Pour finir, j'ai traduit le site web en anglais et j'ai réalisé des démarchages ainsi que des emailings à des fins marketing.
          </p>
		 <a target="_blank" href="https://www.gaming-jobs.fr/">
		  <img className="profile" src="images/sample-image.png" alt />
        </a>
		</div>
      </div> {/* item end */}
      <div className="row item">
        <div className="twelve columns">
          <h3>Le Temple du Fitness</h3>
          <p className="info">Assistant communication <span>•</span> <em className="date">Janvier 2017</em></p>
          <p>
            Ce stage m'a permis de me familiariser avec le travail en entreprise. J'ai eu l'ocassion de créer et gérer le compte Twitter de la salle de sport, de
			réaliser un jeu concours afin d'augmenter le nombre d'inscrits à l'année. De plus,  j'ai réalisé des affiches pour promouvoir les réseaux sociaux de l'entreprise. 
          </p>
		  <a target="_blank" href="https://letempledufitness.fr/">
		  <img className="logocv1" src="images/logo-temple-du-fitness.png" alt />
        </a>
		</div>
      </div> {/* item end */}
    </div> {/* main-col end */}
  </div> {/* End Work */}
  {/* Skills
----------------------------------------------- */}
  <div className="row skill">
    <div className="three columns header-col">
      <h1><span>Compétences</span></h1>
    </div>
    <div className="nine columns main-col">
      <p>
      </p>
      <div className="bars">
        <ul className="skills">
          <h2>Developpement web</h2>
          <li><span className="bar-expand css" /><em>CSS</em></li>
          <li><span className="bar-expand html5" /><em>HTML5</em></li>
          <li><span className="bar-expand jquery" /><em>Reactjs</em></li>
		  <h2>Communication</h2>
		  <li><span className="bar-expand photoshop" /><em>Photoshop</em></li>
          <li><span className="bar-expand illustrator" /><em>Anglais</em></li>
          <li><span className="bar-expand wordpress" /><em>Wordpress</em></li>
		  <li><span className="bar-expand office" /><em>Suite office</em></li>
        </ul>
      </div>{/* end skill-bars */}
    </div> {/* main-col end */}
  </div> {/* End skills */}
</section>
      </React.Fragment>
    );
  }
}