import React, { Component } from 'react';
import '../App.js';
class Counter extends React.Component {
  
  constructor() {
    super();
    this.state = {
      value: 0
    };
  }
  
  componentWillMount() {
    this.count();
  }
  
  count() {
    const { progress } = this.props;
    const id = setInterval(() => {
      let { value } = this.state;
      value = value + 1;
      if (value === progress) {
        clearInterval(id);
      }
      this.setState({
        value
      });
    }, 2000 / progress);
  }
  
  render() {
    const { width } = this.props;
    return (
      <text
        x="250"
        y="275"
        fontSize={400/5}
      >
        {this.state.value}%
      </text>
    );
  }
}

export default Counter;